.title {
  font-size: 36px;
  text-align: center;
}

.wrapper {
  display: grid;
  grid-template-rows: 200px auto;
  gap: 30px;
  justify-content: center;
}

.block {
  width: 40vw;
  border: 1px solid;
  border-radius: 10px;
  padding: 15px;
  background-color: rgb(231, 233, 240);
  box-shadow: 0px 32px 36px -32px rgba(31, 38, 135, 0.7);

  &Title {
    margin-top: 0;
    margin-bottom: 15px;
    text-align: center;
    font-size: 25px;
    font-weight: 500;
  }
}
